<template>
  <fixed-layout>
    <template slot="header">
      <!--<topbar>-->
      <!--<template slot="before">-->
      <!--<b class="iconfont icon-back" @click="$router.go(-1)"></b>-->
      <!--</template>-->
      <!--教练认证-->
      <!--<template slot="after">-->
      <!--&lt;!&ndash;<button class="btn" @click="handNext">发送</button>&ndash;&gt;-->
      <!--</template>-->
      <!--</topbar>-->
      <mt-header title="教练认证">
        <router-link to="/" slot="left">
          <mt-button icon="back" @click="$router.go(-1)"></mt-button>
        </router-link>
        <!--<mt-button @click="handNext" slot="right">下一步</mt-button>-->
      </mt-header>
    </template>
    <mt-field type="tel" label="联系方式" placeholder="请填写正确的手机号码用于收取短信" class="right" v-model="phone"></mt-field>
    <mt-field type="mail" label="邮箱" placeholder="请填写邮箱" class="right" v-model="email"></mt-field>
    <mt-field label="所属城市" placeholder="请输入所在城市" class="right" v-model="city"></mt-field>
    <mt-field label="微信号" placeholder="请填写微信号" class="right" v-model="wxId"></mt-field>
    <mt-field type="number" label="银行账号" placeholder="请填写银行账号" class="right" v-model="bankNo"></mt-field>
    <mt-field type="text" label="所属机构" placeholder="请填写您所属的机构" class="right" v-model="institutionName"></mt-field>
    <mt-cell
      title="生日"
      :value="formatBirth"
      is-link
      @click.native="$refs.pickerBirth.open()">
    </mt-cell>
    <mt-datetime-picker
      :startDate="new Date('1900')"
      ref="pickerBirth"
      type="date"
      v-model="birth">
    </mt-datetime-picker>
    <mt-field type="text" label="语言" placeholder="请填写熟悉的语言" class="right" v-model="lang"></mt-field>
    <mt-cell title="上传头像"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <template v-if="headimg.url||headimg.base64">
            <section class="uploadImage">
              <img :src="headimg.url||headimg.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(headimg)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'headimg')"></file-image>
          </template>
        </li>
      </ul>
    </section>
    <mt-cell title="个人主页图" value="请上传横图"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <template v-if="mainimg.url||mainimg.base64">
            <section class="uploadImage">
              <img :src="mainimg.url||mainimg.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(mainimg)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'mainimg')"></file-image>
          </template>
        </li>
      </ul>
    </section>
    <mt-cell title="个人简介"></mt-cell>
    <mt-field type="textarea" placeholder="请简单介绍下自己，会有更多人对您感兴趣哦" v-model="desc"></mt-field>
    <mt-cell title="上传认证材料" value="请上传您的身份证正反面和教练证正反面"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <template v-if="idcardF.url||idcardF.base64">
            <section class="uploadImage">
              <img :src="idcardF.url||idcardF.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(idcardF)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'idcardF')"></file-image>
          </template>
          <h4>身份证正面</h4>
        </li>
        <li>
          <template v-if="idcardB.url||idcardB.base64">
            <section class="uploadImage">
              <img :src="idcardB.url||idcardB.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(idcardB)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'idcardB')"></file-image>
          </template>
          <h4>身份证反面</h4>
        </li>
        <li>
          <template v-if="coachF.url||coachF.base64">
            <section class="uploadImage">
              <img :src="coachF.url||coachF.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(coachF)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'coachF')"></file-image>
          </template>
          <h4>教练证正面</h4>
        </li>
        <li>
          <template v-if="coachB.url||coachB.base64">
            <section class="uploadImage">
              <img :src="coachB.url||coachB.base64"/>
              <b class="iconfont icon-close" @click="handDeleteImage(coachB)"></b>
            </section>
          </template>
          <template v-else>
            <file-image @load="(e,file)=>handLoadImg(e,file,'coachB')"></file-image>
          </template>
          <h4>教练证反面</h4>
        </li>
      </ul>
    </section>
    <section class="ft">
      <p class="agreement"><input type="checkbox"/><a href="">同意《号角平台商户服务协议》</a></p>
      <mt-button type="primary" @click="handSubmit">确认</mt-button>
    </section>
  </fixed-layout>
</template>

<script>
  import { mapState } from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import FileImage from '@/components/file-image.vue'

  export default {
    name: 'coach',
    components: {
      FixedLayout,
      Topbar,
      FileImage
    },
    data () {
      return {
        city: '',
        phone: null,
        email: '',
        wxId: '',
        bankNo: '',
        institutionName: '',
        birth: new Date(),
        lang: '',
        desc: '',
        headimg: {
          file: null,
          base64: '',
          url: ''
        },
        mainimg: {
          file: null,
          base64: '',
          url: ''
        },
        idcardF: {
          file: null,
          base64: '',
          url: ''
        },
        idcardB: {
          file: null,
          base64: '',
          url: ''
        },
        coachF: {
          file: null,
          base64: '',
          url: ''
        },
        coachB: {
          file: null,
          base64: '',
          url: ''
        },
      }
    },
    computed: {
      ...mapState({}),
      formatBirth () {
        let y = this.birth.getFullYear(), m = this.birth.getMonth() + 1, d = this.birth.getDate()
        return `${y}-${m < 10 ? 0 : ''}${m}-${d < 10 ? 0 : ''}${d}`
      }
    },
    watch: {},
    created () {
      if (this.$route.query.coachId) {
        this.$http.get(this.$port.AUTH_COACH, {
          params: {
            coachId: this.$route.query.coachId
          }
        }).then(res => {
          // res.code = 200
          // res.data = {
          //   city: 123,
          //   birth: '2018-09-03',
          //   headimg: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          //   mainimg: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          //   idcardF: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          //   idcardB: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          //   coachF: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          //   coachB: 'https://www.baidu.com/img/bd_logo1.png?where=super',
          // }
          if (res.code === 200) {
            this.city = res.data.city
            this.phone = res.data.phone
            this.email = res.data.email
            this.wxId = res.data.wxId
            this.bankNo = res.data.bankNo
            this.institutionName = res.data.institutionName
            this.birth = new Date(res.data.birth)
            this.lang = res.data.lang
            this.desc = res.data.desc
            this.headimg.url = res.data.headimg
            this.mainimg.url = res.data.mainimg
            this.idcardF.url = res.data.idcardF
            this.idcardB.url = res.data.idcardB
            this.coachF.url = res.data.coachF
            this.coachB.url = res.data.coachB
          }
        })
      }
    },
    methods: {
      handDeleteImage (obj) {
        obj.file = null
        obj.base64 = ''
        obj.url = ''
      },
      uploadImg (file) {
        return new Promise((resolve, reject) => {
          let fd = new FormData()
          fd.append('imgType', 1)
          fd.append('upload', file)
          this.$http.post(this.$port.UPLOAD_IMG, fd).then(res => {
            resolve(res)
          })
        })
      },
      handLoadImg (e, file, prop) {
        console.log('handLoadImg', e, file, prop)
        this[prop].file = file
        this[prop].base64 = e.currentTarget.result
        this.uploadImg(file).then(res => {
          if (res.code === 200) {
            this[prop].url = res.data
          } else {
            alert(res.msg)
          }
        })
      },
      handSubmit () {
        let params = {
          city: this.city,
          phone: this.phone,
          email: this.email,
          wxId: this.wxId,
          bankNo: this.bankNo,
          institutionName: this.institutionName,
          birth: this.formatBirth,
          lang: this.lang,
          headimg: this.headimg.url,
          mainimg: this.mainimg.url,
          idcardF: this.idcardF.url,
          idcardB: this.idcardB.url,
          coachF: this.coachF.url,
          coachB: this.coachB.url,
          desc: this.desc,
        }
        this.$http.post(this.$port.AUTH_COACH, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {

          }
          alert(res.msg)
        })
      }
    }
  }
</script>
<style lang="scss">
</style>
<style lang="scss" scoped>
  .m-uploads {
    background-color: #fff;
    padding: 0 10px;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        position: relative;
        flex: 1;
        b {
          background-color: rgba(0, 0, 0, 0.5);
          text-align: center;
          font-weight: 400;
          font-size: 8px;
          color: #fff;
          width: 20px;
          height: 20px;
          position: absolute;
          right: 0;
          top: 0;
        }
        h4 {
          font-size: 12px;
          text-align: center;
          font-weight: 400;
          color: #333;
          padding-top: 5px;
        }
      }
    }
    .ui-upload {
      width: 110px;
      height: 110px;
    }
  }

  .uploadImage {
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
    }
  }

  .ft {
    padding: 15px 10px;
    border-top: solid #e5e5e5 1px;
    .agreement {
      font-size: 12px;
      margin-bottom: 10px;
      input[type='checkbox'] {
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    .mint-button {
      width: 100%;
    }
  }
</style>



